"use server";

import { revalidatePath } from "next/cache";
import {
  getAllCandies,
  incrementCandy,
  getCandyById,
  deleteCandy as deleteCandyByName,
  setCandyCount,
  getCandyByName,
  type Candy,
} from "@/lib/candy";
import { getDb } from "@/lib/db";

/**
 * Add candy (increments if exists, creates if new)
 */
export async function addCandy(name: string, quantity: number): Promise<Candy> {
  if (!name || name.trim() === "") {
    throw new Error("Candy name is required");
  }
  if (quantity < 1) {
    throw new Error("Quantity must be at least 1");
  }
  
  const candy = incrementCandy(name.trim(), quantity);
  revalidatePath("/");
  return candy;
}

/**
 * Get all candies
 */
export async function getAllCandiesAction(): Promise<Candy[]> {
  return getAllCandies();
}

/**
 * Update candy by ID (updates name and/or count)
 */
export async function updateCandy(
  id: number,
  name: string,
  count: number
): Promise<Candy> {
  if (!name || name.trim() === "") {
    throw new Error("Candy name is required");
  }
  if (count < 0) {
    throw new Error("Count cannot be negative");
  }

  const existing = getCandyById(id);
  if (!existing) {
    throw new Error("Candy not found");
  }

  const db = getDb();
  const trimmedName = name.trim();

  // If name changed, need to check if new name already exists
  if (trimmedName !== existing.candy_name) {
    const nameExists = getCandyByName(trimmedName);
    if (nameExists && nameExists.id !== id) {
      throw new Error("Candy with this name already exists");
    }
  }

  // Update both name and count
  const stmt = db.prepare(
    "UPDATE candy_counts SET candy_name = ?, count = ? WHERE id = ?"
  );
  stmt.run(trimmedName, count, id);

  const updated = getCandyById(id);
  if (!updated) {
    throw new Error("Failed to update candy");
  }

  revalidatePath("/");
  return updated;
}

/**
 * Delete candy by ID
 */
export async function deleteCandy(id: number): Promise<boolean> {
  const existing = getCandyById(id);
  if (!existing) {
    return false;
  }

  const result = deleteCandyByName(existing.candy_name);
  revalidatePath("/");
  return result;
}

